<template>
  <v-container v-if="group">
    <v-row>
      <v-col>
        <div class="text px-2" @click="tts(group.grouping)">
          {{ group.grouping }}
        </div>
      </v-col>
    </v-row>
    <v-row>
      <!-- <v-text-field v-model="text"></v-text-field>
      <v-btn @click="tts(text)"></v-btn>-->
      <audio controls="controls" ref="audio" style="display:none">
        <source :src="audioFile" type="audio/mp3" />
      </audio>
      <v-col
        cols="auto"
        v-for="(word, index) in group.wordList"
        :key="index"
        class="pointer"
      >
        <div class="text px-2" @click="tts(word)">
          <div v-html="highlight(word)"></div>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios"
import { mapState } from "vuex"

export default {
  data: () => ({
    testGroup: ["hello", "world"],
    audioFile: null,
    text: "hello",
    group: false,
  }),
  computed: {
    ...mapState(["groupId", "groups"]),
  },
  mounted() {
    this.group = this.groups.filter((obj) => obj.id == this.groupId)[0]
  },
  methods: {
    convertAudioContentToBinary(audioContent) {
      const raw = window.atob(audioContent)
      const rawLength = raw.length
      const array = new Uint8Array(new ArrayBuffer(rawLength))

      for (let i = 0; i < rawLength; i++) {
        array[i] = raw.charCodeAt(i)
      }
      return array
    },
    async tts(text) {
      const api =
        "https://texttospeech.googleapis.com/v1beta1/text:synthesize?key=AIzaSyAdVp77_raiBhM1nS8ryZY52rKFdHl96o0"
      const request = {
        input: { text: text },
        voice: { languageCode: "en-US", ssmlGender: "FEMALE" },
        audioConfig: { audioEncoding: "MP3" },
      }
      const result = await axios.post(api, request)

      //console.log(result.data)
      const binary = this.convertAudioContentToBinary(result.data.audioContent)
      const blob = new Blob([binary], { type: "audio/mp3" })
      const blobUrl = URL.createObjectURL(blob)

      this.audioFile = blobUrl
      this.$refs.audio.pause()
      this.$refs.audio.load()
      this.$refs.audio.oncanplaythrough = this.$refs.audio.play()
    },
    highlight(word) {
      const regex = new RegExp(`(.*)(${this.group.grouping})(.*)`, "i")
      let temp = word.match(regex)
      if (temp === null) return word
      temp.shift()
      temp[1] = `<p class="highlight" style="display:inline-block">${temp[1]}</p>`
      temp = temp.join("")
      return temp
      // return word.split(this.group.grouping).splice(1, 0, `<p class="highlight">${this.group.grouping}</p>`).join()
    },
  },
}
</script>

<style>
.text {
  border: 1px solid black;
  border-top: 0px;
  font-size: 5em;
  text-align: center;
}
.text:first-child {
  border-top: 1px solid black;
}
.highlight {
  color: red;
}
</style>
